.nav_button{
    position: relative;
    overflow: hidden;
    padding: 10px;
    background-image: linear-gradient(#020202, #181818);
}

header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 10px;
    z-index: 1;
    background-image: linear-gradient(#020202, #181818);
    background-size: cover;
    background-position: center;
    align-items: center;
    justify-content: space-between;
  }
  
  nav ul {
    display: flex;
    justify-content: flex-end;
  
  }
  
  .logo {
    margin-right: auto;
  }
  
  nav ul li {
    display: inline-block;
    margin-right: 10px;
  }
  
  nav ul li a {
    color: #fff;
    text-decoration: none;
  }

  
.nav_button::after{
    content: "";
    display: block;
    width:100%;
    height: 4px;
    background-color: #fff;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    transition: tranform 10s ease;

}
.nav_button:hover::after{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.nav_button activate{
    background-color: #4CAF50; /* Green background when active */
    color: white;
}
#projects {
  display: flex;
  flex-direction: column;
  padding: 40px;
  align-items: center;
}

#projectDetails {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-gap: 0.5rem;
  padding: 20px;
}

#projects h2 {
  font-size: 2.25rem;
  color: white;
}

.proj {
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(#020202, #181818);
  border-radius: 10px;
  color: white;
  padding: 20px;
  opacity: 0;
  margin-bottom: 20px;
  align-items: center;
  justify-content: center;
  -webkit-animation: fade-in 1s ease-in-out forwards, slide-up 1s ease-in-out forwards;
          animation: fade-in 1s ease-in-out forwards, slide-up 1s ease-in-out forwards;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.proj:hover {
  -webkit-transform: scale(1.05) rotate(5deg);
          transform: scale(1.05) rotate(5deg);
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }

  to {
    opacity: 0.8;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }

  to {
    opacity: 0.8;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes slide-up {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }

  to {
    opacity: 0.8;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-up {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }

  to {
    opacity: 0.8;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes rotate {
  from {
    opacity: 0;
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }

  to {
    opacity: 0.8;
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
}

@keyframes rotate {
  from {
    opacity: 0;
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }

  to {
    opacity: 0.8;
    -webkit-transform: rotateX(0);
            transform: rotateX(0);
  }
}

